.menuBtn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 20px;
}

.iconOpen {
  display: block;
  width: 14px;
  color: var(--text-primary-color);
  fill: currentColor;
}

.iconClose {
  display: block;
  width: 14px;
  color: var(--text-primary-color);
  fill: currentColor;
}

.link {
  color: var(--text-primary-color);
}
    .menu {
      position: absolute;
      z-index: 100;
      left: 0;
      top: var(--header-constant-height);
      width: 100vw;
      min-height: 702px;
      height: calc(100vh - var(--header-constant-height));
      background-color: var(--background-primary-color);
    }

@media screen and (max-width: 767px) {

  .tab {
    height: 76px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-top: 1px solid var(--header-border-color);
  }
}

.logout {
  width: 16px;
  height: 16px;
}

:global(.body-no-scroll) {
  overflow: hidden;
}

